import PropTypes from 'prop-types';
import PhotoAlbum from 'react-photo-album';
import { useState, useEffect } from 'react';
import { EditorState } from 'draft-js';
import MemoryCarousel from './MemoryCarousel';
import MemoryContentStructure from './MemoryContentStructure';
import MemoryTitle from './MemoryTitle';
import MemoryDate from './MemoryDate';
import MemoryStory from './MemoryStory';
import api from '../Api';
import './memory.css';

export default function MemoryContent({
  title,
  date,
  editorState,
  images,
  bucket,
  memoryID,
  authURL,
}) {
  const [open, setOpen] = useState(false);
  const [edit, setEdit] = useState(false);
  const [activeIndex, setActiveIndex] = useState(0);
  const [layout, setLayout] = useState(null);
  const maxSteps = images.length;

  const UpdateDetails = async (item, value) => {
    try {
      const resp = await api.updateMemoryDetails(
        bucket,
        authURL,
        memoryID,
        item,
        value,
      );
      return resp;
    } catch (error) {
      console.log(error);
      return false;
    }
  };

  useEffect(() => {
    let twidth = 0;
    let theight = 0;
    images.forEach(({ height, width }) => {
      if (height > width) theight += 1;
      else twidth += 1;
    });
    if (twidth > theight) setLayout('rows');
    else setLayout('columns');
  }, [images]);

  const handleClose = () => setOpen(false);

  const handleBack = () => {
    setActiveIndex(
      (prevActiveStep) => (prevActiveStep - 1 + maxSteps) % maxSteps,
    );
  };

  const handleNext = () => {
    setActiveIndex((prevActiveStep) => (prevActiveStep + 1) % maxSteps);
  };

  const handlePhotoClick = (photo, event, index) => {
    setOpen(true);
    setActiveIndex(index);
  };

  const handleEditClick = () => setEdit(true);

  const photoLayout = (
    <PhotoAlbum
      layout={layout}
      photos={images}
      onClick={handlePhotoClick}
      spacing={2}
    />
  );

  const memoryTitle = (
    <MemoryTitle
      editable={edit}
      text={title}
      handleTitleUpdate={UpdateDetails}
    />
  );

  const memoryDate = (
    <MemoryDate editable={edit} prevDate={date} handleDateUpdate={UpdateDetails} />
  );

  const storyLayout = (
    <MemoryStory
      editable={edit}
      prevEditorState={editorState}
      handleEditorStateUpdate={UpdateDetails}
    />
  );
  console.log('Memory Content', edit, memoryTitle);
  return (
    <>
      <MemoryContentStructure
        header={memoryTitle}
        subheader={memoryDate}
        photoLayout={photoLayout}
        storyLayout={storyLayout}
        memoryID={memoryID}
        bucket={bucket}
        authURL={authURL}
        handleEditClick={handleEditClick}
      />
      <MemoryCarousel
        open={open}
        title={title}
        date={date}
        images={images}
        index={activeIndex}
        handleNext={handleNext}
        handleBack={handleBack}
        handleClose={handleClose}
      />
    </>
  );
}

MemoryContent.defaultProps = {
  title: 'Memory Title',
  date: 'Memory Date',
  editorState: EditorState.createWithText('Memory Text'),
};

MemoryContent.propTypes = {
  title: PropTypes.string,
  date: PropTypes.string,
  editorState: PropTypes.instanceOf(EditorState),
  memoryID: PropTypes.string.isRequired,
  bucket: PropTypes.string.isRequired,
  authURL: PropTypes.string.isRequired,
  images: PropTypes.arrayOf(
    PropTypes.shape({
      src: PropTypes.string,
      width: PropTypes.number,
      height: PropTypes.number,
    }),
  ).isRequired,
};
